











import { createComponent, PropType } from '@vue/composition-api';

const TodoList = createComponent({
  props: {
    todos: {
      type: Array as PropType<string[]>,
      required: true,
    },
    delete: {
      type: Function,
      required: true,
    },
  },
  setup(props) {
    const onDelete = (index: number) => {
      props.delete(index);
    };
    return {
      onDelete,
    };
  }
});

export default TodoList;
